 var is = {
   phone:function(phone){
     return (/(^(13\d|14[57]|15[^4,\D]|17[678]|18\d)\d{8}|170[059]\d{7})$/.test(phone));
   }
 }
 var myExtends = function(){
   String.prototype.decodeHTML = function() {
    var str = this.replace(new RegExp('\r?\n','g'), '<\p><p>');
    return str;
  };
 }
